import React from 'react';
import PropTypes from 'prop-types';
import ItemBlock from '../components/ItemBlock';
import styled from 'styled-components';
import compose from 'recompose/compose';
import withStateHandlers from 'recompose/withStateHandlers';
import withProps from 'recompose/withProps';
import withHandlers from 'recompose/withHandlers';
import Modal from '../components/Modal';

/** images */
import nanGua from '../assets/images/南瓜鍋.jpg';
import gaLi from '../assets/images/咖哩鍋.jpg';
import siChuanMaLa from '../assets/images/四川麻辣鍋.jpg';
import peiGenNiuRou from '../assets/images/培根牛肉鍋.jpg';
import daChangChouChou from '../assets/images/大腸臭臭鍋.jpg';
import shaChaZhuRou from '../assets/images/沙茶豬羊肉鍋.jpg';
import paoCai from '../assets/images/泡菜鍋.jpg';
import taiShiSuanLa from '../assets/images/泰式酸辣鍋.jpg';
import haiXianDouFu from '../assets/images/海鮮豆腐鍋.jpg';
import haiXianYuPian from '../assets/images/海鮮魚片鍋.jpg';
import niuNaiQiSi from '../assets/images/牛奶起司鍋.jpg';
import fanQie from '../assets/images/番茄鍋.jpg';
import suShiFanQie from '../assets/images/素食番茄鍋.jpg';
import suShi from '../assets/images/素食鍋.jpg';
import yangRouLu from '../assets/images/羊肉爐鍋(冬季限定).jpg';
import shuCaiXiangGu from '../assets/images/蔬菜香菇鍋.jpg';
import yaoShanYangZhuRou from '../assets/images/藥膳羊豬肉鍋.jpg';
import tongPanKaoRou from '../assets/images/銅盤烤肉套餐.jpg';
import xueHuaNiuRou from '../assets/images/雪花牛肉鍋.jpg';
import shuangJiangZhuRou from '../assets/images/霜降豬肉鍋.jpg';
import yaXie from '../assets/images/鴨血鍋.jpg';

/** premium menu set */
import proSet from '../assets/images/pro/海鮮梅花豬肉鍋.jpg';
import haiXianMeiHuaZhuRouGuo from '../assets/images/pro/海鮮梅花豬肉鍋海報.jpg';
import haiXuanXueHuaNiuRouGuo from '../assets/images/pro/海鮮雪花牛肉鍋海報.jpg';
import maLaHaiXianMeiHuaZhuRouGuo from '../assets/images/pro/麻辣海鮮梅花豬肉鍋海報.jpg';
import maLaHaiXianXueHuaNiuRouGuo from '../assets/images/pro/麻辣海鮮雪花牛肉鍋海報.jpg';
import hongShaoYangRouLu from '../assets/images/pro/紅燒羊肉爐海報.jpg';

const ItemsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const menuPage = ({ items, isModalOpen, toggleModal, renderProMenuContent }) => {
  return (
    <ItemsContainer width="15%">
      <Modal
        visible={isModalOpen}
        width={80}
        height={80}
        measure="%"
        onClose={toggleModal}
        closeMaskOnClick={false}
      >
        {renderProMenuContent()}
      </Modal>
      {items.map(item => (
        <ItemBlock
          key={item.title}
          className="menu-item"
          {...item}
        />
      ))}
    </ItemsContainer>
  );
};

menuPage.propTypes = {
  renderProMenuContent: PropTypes.func.isRequired,
  toggleModal: PropTypes.func.isRequired,
  isModalOpen: PropTypes.bool.isRequired,
  items: PropTypes.arrayOf(PropTypes.object),
};

menuPage.defaultProps = {
  items: []
};

export default compose(
  withStateHandlers({ isModalOpen: false }, {
    toggleModal: ({ isModalOpen }) => () => ({ isModalOpen: !isModalOpen })
  }),
  withProps(({ toggleModal }) => ({
    premiumItems: [
      { imgSrc: haiXianMeiHuaZhuRouGuo, title: '海鮮梅花豬肉鍋海報' },
      { imgSrc: haiXuanXueHuaNiuRouGuo, title: '海鮮雪花牛肉鍋海報' },
      { imgSrc: maLaHaiXianMeiHuaZhuRouGuo, title: '麻辣海鮮梅花豬肉鍋海報' },
      { imgSrc: maLaHaiXianXueHuaNiuRouGuo, title: '麻辣海鮮雪花牛肉鍋海報' },
      { imgSrc: hongShaoYangRouLu, title: '紅燒羊肉爐海報' },
    ],
    items: [
      {
        imgSrc: proSet,
        title: '豪華年菜套餐',
        price: '3-4 人份',
        subtitle: '優質重量級海鮮、牛肉、豬肉等 3-4 人豪華套餐附湯包',
        btnText: '閱讀更多',
        btnOnClick: toggleModal,
      },
      { imgSrc: gaLi, title: '銅盤烤肉套餐', price: 290, subtitle: '牛、豬、羊 任選兩樣, 附白飯、泡菜、生菜' },
      { imgSrc: siChuanMaLa, title: '羊肉爐鍋(冬季限定)', price: 210 },
      { imgSrc: haiXianDouFu, title: '海鮮豆腐鍋', price: 130 },
      { imgSrc: paoCai, title: '泡菜鍋', price: 140 },
      { imgSrc: suShi, title: '素食鍋', price: 130 },
      { imgSrc: yaXie, title: '鴨血鍋', price: 140 },
      { imgSrc: yaoShanYangZhuRou, title: '藥膳羊(豬)肉鍋', price: 140 },
      { imgSrc: shuangJiangZhuRou, title: '霜降豬肉鍋', price: 170 },
      { imgSrc: xueHuaNiuRou, title: '雪花牛肉鍋', price: 170 },
      { imgSrc: suShiFanQie, title: '素食番茄鍋', price: 140 },
      { imgSrc: shuCaiXiangGu, title: '蔬菜香菇鍋', price: 150 },
      { imgSrc: haiXianYuPian, title: '海鮮魚片鍋', price: 150 },
      { imgSrc: fanQie, title: '番茄鍋', price: 150 },
      { imgSrc: peiGenNiuRou, title: '培根牛肉鍋', price: 150 },
      { imgSrc: taiShiSuanLa, title: '泰式酸辣鍋', price: 150 },
      { imgSrc: niuNaiQiSi, title: '牛奶起司鍋', price: 170 },
      { imgSrc: shaChaZhuRou, title: '沙茶豬(羊)肉鍋', price: 140 },
      { imgSrc: gaLi, title: '咖哩鍋', price: 140 },
      { imgSrc: nanGua, title: '南瓜鍋', price: 150 },
      { imgSrc: daChangChouChou, title: '大腸臭臭鍋', price: 130 },
    ]
  })),
  withHandlers({
    renderProMenuContent: ({ premiumItems }) => () => {
      return (
        <div>
          <div style={{ height: 34, paddingBottom: 8 }}>
            豪華年菜套餐
          </div>
          <div
            style={{
              overflow: 'scroll',
              height: '70vh'
            }}
          >
            {premiumItems.map(item => (
              <div
                key={item.title}
              >
                <img src={item.imgSrc} alt={item.title} />
              </div>
            ))}
          </div>
        </div>
      );
    }
  })
)(menuPage);


