import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import LinkButton from '../LinkButton';
import media from '../../media';

import withHandlers from 'recompose/withHandlers';

const StyledBlock = styled.div`
  border-top: 1px solid orange;
  padding: 16px 8px;
  color: gray;
  background-color: white;
  margin: 0 auto;
  margin: 8px 4px;
  position: relative;
  width: 210px;

  ${media.mobile`
    width: 80%;
  `}
`;

const ImgContainer = styled.div`
  max-height: 150px;
  overflow: hidden;
  padding: 16px;
`;

const Img = styled.img`
  width: 100%
`;

const BlockContent = styled.div`
  padding: 16px 0;
  text-align: center;
  flex: 1;

  > .title {
    margin: 0;
  }

  > .subtitle {
    margin: 0;
    padding-top: 8px;
  }
`;

const PriceTag = styled.div`
  position: absolute;
  top: 0;
  right: -5px;
  color: white;
  background-color: orange;
  border-radius: 16px;
  font-size: 24px;
  padding: 8px 12px;
`;

const ItemBlock = ({
  imgSrc,
  title,
  subtitle,
  price,
  renderPriceTag,
  btnText,
  btnOnClick,
}) => {
  return (
    <StyledBlock>
      <PriceTag>{renderPriceTag()}</PriceTag>
      <ImgContainer>
        <Img src={imgSrc} alt="頂豐小火鍋no1" />
      </ImgContainer>
      <BlockContent>
        <h3 className="title">{title}</h3>
        <p className="subtitle">{subtitle}</p>
        {btnText && btnOnClick &&
          <div>
            <LinkButton onClick={btnOnClick}>{btnText}</LinkButton>
          </div>
        }
      </BlockContent>
    </StyledBlock>
  );
}

ItemBlock.propTypes = {
  price: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]).isRequired,
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string,
  imgSrc: PropTypes.string.isRequired,
  renderPriceTag: PropTypes.func.isRequired,
  btnText: PropTypes.string,
  btnOnClick: PropTypes.func,
};

export default withHandlers({
  renderPriceTag: ({ price }) => () => {
    if (typeof price === 'string') {
      return price;
    }
    return `$${price}`;
  }
})(ItemBlock);


