import { css } from 'styled-components';

const media = {
  mobile: (...args) => css`
    @media only screen and (max-width: 510px) {
      ${ css(...args) }
    }
  `,
  tablet: (...args) => css`
    @media only screen and (min-width: 511px) and (max-width: 768px) {
      ${ css(...args) }
    }
  `,
  desktop: (...args) => css`
    @media only screen and (min-width: 769px) {
      ${ css(...args) }
    }
  `,
};

export default media;


